.noticiaContemp {
  display: flex;
  flex-direction: column;
  align-items: center;

  img {
    height: 40vh;
  }
  .paraContemp {
    display: flex;
    flex-direction: column;
    text-align: left;
    overflow-y: scroll;
    height: 50vh;
    p {
      margin-top: 0.5rem;
    }
    .tituloContemp {
      font-weight: 500;
      font-size: 1.1rem;
    }
  }
}
.contenedorContemporaneo {
  width: 50%;

  @media (max-width: 600px) {
    width: 100%;
  }
}
